.App{
  min-height : 100vh;
}

.searchForm{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.searchInput{
  width: 50%;
  border: 1px solid #cccccc;
  padding: 10px;
}

.searchBtn{
  background-color: #336699;
  border: none;
  padding: 10px 20px;
  color:whitesmoke;
}

.Recipe{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding:20px;

}