.recipe{
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    box-shadow: 0px 5px 2px #eeeeee;
    margin: 20px;
    justify-content: space-around;
    background-color: white;
    align-items: center;
    min-width: 40%;
}

.image{
    width:100%;
    height: 100%;
}